.projects {
    display: none;
    width: 100%;
    height: 100vh;
}

.project {
    background: var(--primary);
    color: var(--white);
    margin-top: 50px;
    overflow: hidden;
    min-height: 70vh;
    position: relative;
}

@media (max-width: 768px) {
    .project {
        min-height: 60vh;
        margin-top: 10px;
    }
}

.project-curtain {
    background: var(--secondary);
    width: 0;
    height: 100%;
    position: absolute;
    top: 0;
    z-index: 1001;
}


/* project-content */

.project-content {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    padding: 15px;
}


/* project title */

.project-title {
    margin-bottom: 5px;
    color: var(--secondary);
    letter-spacing: -1.5px;
}


/* Project Tags */

.project-tags {
    margin-bottom: 15px;
}

.tag {
    font-size: 12px;
    position: relative;
    padding-left: 8px;
}

.tag:not(:last-child) {
    margin-right: 10px;
}

.tag::before {
    content: '';
    position: absolute;
    left: 0;
    width: 5px;
    height: 5px;
    border-radius: 50%;
    background: var(--white);
}


/* project body */

.project-body {
    height: 90%;
    gap: 15px;
}

.project-details,
.project-image {
    width: 50%;
}

.project-image {
    justify-content: flex-end;
    margin-bottom: 15px;
}

.project-image img {
    max-width: 400px;
}

@media (max-width:850px) {
    .project-image {
        display: none;
    }
    .project-details {
        width: 100%;
    }
}


/* project links */

.project-links {
    gap: 20px;
}

.project-link {
    color: var(--white) !important;
    background: var(--secondary);
    border-radius: 5px;
    padding: 1px 10px;
}

.project-link:hover {
    background: var(--secondary-hover);
}


/* Project Controlls */

.project-controls {
    margin-top: 20px;
    gap: 50px;
}

.project-controls svg {
    cursor: pointer;
    transition: all .5s;
}

.project-controls svg:hover {
    transform: scale(1.2);
}